import { useState, useEffect } from "react";

import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { Snackbar, Spreadsheet } from "../../components";
import BarChart from "./charts";

import { useQuery, useSubscription } from "@apollo/client";
import { SUBSCRIBE_OFFICE_EVENTS } from "../../graphql/users";
import { GET_REPORT_SUMMARY } from "../../graphql/reports";
import { GET_DOCUMENT_SUMMARY, GET_ISO_SUMMARY } from "../../graphql/documents";

interface ChartData {
  name: string;
  data: number[];
}

function formatOfficeAbbr(offices: string[]): string[] {
  return offices.map((office) => {
    return office
      .split(" ")
      .filter((word) => !["of", "the"].includes(word.toLowerCase()))
      .map((word) => {
        if (isNaN(parseInt(word))) return word.charAt(0).toUpperCase();
        else return `-${parseInt(word).toString()}`;
      })
      .join("");
  });
}

export default function DashboardPage() {
  const {
    data: documents,
    error: documentsError,
    refetch: refetchDocuments,
  } = useQuery(GET_DOCUMENT_SUMMARY);

  const {
    data: isoDocuments,
    error: isoDocumentsError,
    refetch: refetchIsoDocuments,
  } = useQuery(GET_ISO_SUMMARY);

  const {
    data: reports,
    error: reportsError,
    refetch: refetchReports,
  } = useQuery(GET_REPORT_SUMMARY);

  const { data: officeEvents, error: subscribeError } = useSubscription(
    SUBSCRIBE_OFFICE_EVENTS
  );

  const [offices, setOffices] = useState<string[]>([]);
  const [isoStats, setIsoStats] = useState<ChartData[]>([]);
  const [documentStats, setDocumentStats] = useState<ChartData[]>([]);
  const [reportStats, setReportStats] = useState<ChartData[]>([]);

  useEffect(() => {
    if (documents) {
      setOffices(
        formatOfficeAbbr(
          documents.getDocumentSummary.map((stat) => stat.office)
        )
      );
    }
  }, [documents]);

  useEffect(() => {
    if (isoDocuments) {
      setIsoStats([
        {
          name: "Reffered",
          data: isoDocuments.getIsoSummary.map((stat) => stat.referred),
        },
        {
          name: "Submitted",
          data: isoDocuments.getIsoSummary.map((stat) => stat.submitted),
        },
        {
          name: "For Approval",
          data: isoDocuments.getIsoSummary.map((stat) => stat.forApproval),
        },
        {
          name: "For Review",
          data: isoDocuments.getIsoSummary.map((stat) => stat.forReview),
        },
        {
          name: "For Correction",
          data: isoDocuments.getIsoSummary.map((stat) => stat.forCorrection),
        },
        {
          name: "For Revision",
          data: isoDocuments.getIsoSummary.map((stat) => stat.forRevision),
        },
        {
          name: "Update Report",
          data: isoDocuments.getIsoSummary.map((stat) => stat.updateReport),
        },
        {
          name: "Closed",
          data: isoDocuments.getIsoSummary.map((stat) => stat.closed),
        },
        {
          name: "Not Actionable",
          data: isoDocuments.getIsoSummary.map((stat) => stat.noaction),
        },
      ]);
    }
  }, [isoDocuments]);

  useEffect(() => {
    if (documents) {
      setDocumentStats([
        {
          name: "Reffered",
          data: documents.getDocumentSummary.map((stat) => stat.referred),
        },
        {
          name: "Submitted",
          data: documents.getDocumentSummary.map((stat) => stat.submitted),
        },
        {
          name: "For Approval",
          data: documents.getDocumentSummary.map((stat) => stat.forApproval),
        },
        {
          name: "For Review",
          data: documents.getDocumentSummary.map((stat) => stat.forReview),
        },
        {
          name: "For Correction",
          data: documents.getDocumentSummary.map((stat) => stat.forCorrection),
        },
        {
          name: "For Revision",
          data: documents.getDocumentSummary.map((stat) => stat.forRevision),
        },
        {
          name: "Update Report",
          data: documents.getDocumentSummary.map((stat) => stat.updateReport),
        },
        {
          name: "Closed",
          data: documents.getDocumentSummary.map((stat) => stat.closed),
        },
        {
          name: "Not Actionable",
          data: documents.getDocumentSummary.map((stat) => stat.noaction),
        },
      ]);
    }
  }, [documents]);

  useEffect(() => {
    if (reports) {
      setReportStats([
        {
          name: "Ongoing",
          data: reports.getReportSummary.map((stat) => stat.pending),
        },
        {
          name: "Submitted",
          data: reports.getReportSummary.map((stat) => stat.submitted),
        },
      ]);
    }
  }, [reports]);

  useEffect(() => {
    if (officeEvents) {
      refetchDocuments();
      refetchReports();
      refetchIsoDocuments();
    }
  }, [officeEvents, refetchDocuments, refetchReports, refetchIsoDocuments]);

  return (
    <Container>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
            mb={5}
          >
            <Typography variant="h4">Good Day 👋</Typography>

            <Spreadsheet />
          </Stack>
        </Grid>

        <Grid item xs={12}>
          <BarChart
            title="Documents"
            subheader=""
            labels={offices}
            series={documentStats}
          />
        </Grid>

        <Grid item xs={12}>
          <BarChart
            title="Reports"
            subheader=""
            labels={offices}
            series={reportStats}
          />
        </Grid>

        <Grid item xs={12}>
          <BarChart
            title="ISO Documents"
            subheader=""
            labels={offices}
            series={isoStats}
          />
        </Grid>
      </Grid>

      <Snackbar
        severity="error"
        message={
          documentsError?.message ||
          subscribeError?.message ||
          reportsError?.message ||
          isoDocumentsError?.message
        }
      />
    </Container>
  );
}
